@import url(~react-genomix/dist/react-genomix.min.css);
/* Partials */
@import url("https://fonts.googleapis.com/css?family=Actor|Roboto|Open+Sans:300|Ubuntu");
.VictoryContainer {
  height: 50% !important; }

.ui.genomix.button {
  font-family: "Ubuntu", sans-serif; }

.banner {
  text-align: center;
  background-color: #1F2D3D; }

.title {
  padding-top: 25px;
  font-family: "Ubuntu", sans-serif;
  color: #1F2D3D; }

.detail {
  padding-top: 0px; }

.info {
  font-size: 18px; }

.banner.title {
  color: #FFFFFF; }

.banner.info {
  color: #FFFFFF; }

.ui.grid > .row.banner.detail {
  color: #D3DCE6; }

.ui.grid > .row.banner.action {
  padding-bottom: 25px; }

.about-section {
  text-align: left; }

.about.title {
  text-align: center; }

.recent-news.title {
  text-align: left; }

.ui.list.recent-news {
  font-size: 18px; }

.public_fixedDataTableRow_main.warning .public_fixedDataTableCell_main {
  background-color: #FFC82C;
  color: #1F2D3D;
  opacity: 0.5; }

.public_fixedDataTableRow_main.error .public_fixedDataTableCell_main {
  background-color: #FF4949;
  color: #1F2D3D;
  opacity: 0.7; }

/* global styles */
body {
  font-family: "Ubuntu", sans-serif;
  background: #F9FAFC; }



